.goods-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  text-align: center;
  width: 200px;
  margin: 10px;
}

img {
  max-width: 150px;
  object-fit: cover;
  border-radius: 4px;
}

h3 {
  font-size: 18px;
  margin: 12px 0;
}

p {
  font-size: 16px;
  color: #333;
}